import { ChangeDetectionStrategy, Component } from '@angular/core';
import { AgFilterComponent } from 'ag-grid-angular';
import { AgPromise, IDoesFilterPassParams, IFilterParams } from 'ag-grid-community';

interface RangeFilter {
    min: number;
    max: number;
}

export interface MultiRangeFilterModel {
    rangeFilters: Array<RangeFilter>;
}


@Component({
    selector: 'app-multi-range-filter',
    template: '',
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class MultiRangeFilterComponent implements AgFilterComponent {

    public params: IFilterParams;
    public model: MultiRangeFilterModel;

    isFilterActive(): boolean {
        return this.model?.rangeFilters && this.model.rangeFilters.length > 0;
    }

    doesFilterPass(params: IDoesFilterPassParams): boolean {
        const value: number = this.params.valueGetter(params.node) as number;
        let pass = false;
        this.model?.rangeFilters.forEach(filter => {
            if (this.isInRange(value, filter)) {
                pass = true;
            }
        });
        return pass;
    }

    public isInRange(value: number, range: RangeFilter): boolean {
        if (Number.isNaN(value)) {
            return false;
        }
        if (value > range.max) {
            return false;
        }
        if (value < range.min) {
            return false;
        }
        return true;
    }

    getModel(): unknown {
        return this.model;
    }

    setModel(model: MultiRangeFilterModel): void | AgPromise<void> {
        this.model = model;
    }

    agInit(params: IFilterParams): void {
        this.params = params;
    }

}
