import { Injectable } from '@angular/core';
import {
    ActivatedRouteSnapshot,
    CanActivate,
    Router,
    RouterStateSnapshot
} from '@angular/router';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';

import { Role } from 'src/app/common/models/role.model';
import { User } from 'src/app/common/models/user.model';
import { errorRouteResolver } from 'src/app/pages/error/error-routing.resolver';

import { ApplicationContextQuery } from '../../../store/application-context/application-context.query';

@Injectable({
    providedIn: 'root'
})
export abstract class RoleSecuredPageGuard implements CanActivate {

    public constructor(
        private router: Router,
        private appContextQuery: ApplicationContextQuery
    ) {}

    canActivate(
        _next: ActivatedRouteSnapshot,
        _state: RouterStateSnapshot
    ): Observable<boolean> {
        const currentUserRole$ = this.appContextQuery.getCurrentUserRole$();
        return currentUserRole$.pipe(
            filter(userRole => userRole !== undefined),
            map(currentUserRole => {
                const isAuthorized = this.hasUserRoleAccess(currentUserRole);
                if (isAuthorized) {
                    return true;
                } else {
                    void this.router.navigate([errorRouteResolver.routePath(403)]);
                    return false;
                }
            })
        );
    }

    /**
     * Check that an user have access to this module according to his roles.
     *
     * @param user
     */
    public hasUserAccess(user: User): boolean {
        if (this.getRequiredRole().length < 1) {
            return true;
        } else {
            // eslint-disable-next-line no-loops/no-loops,@typescript-eslint/no-for-in-array
            for (const role in this.getRequiredRole()) {
                if (user.getRoles().findIndex(urole => urole.getCode() === role) >= 0) {
                    return true;
                }
            }
            return false;
        }
    }

    public hasUserRoleAccess(userRoleCode: Role['code']): boolean {
        // If no role is required
        if (this.getRequiredRole().length < 1) {
            return true;
        } else {
            // Check that this role is inside the required one :
            return this.getRequiredRole().findIndex(roleCode => roleCode === userRoleCode) >= 0;
        }
    }

    abstract getRequiredRole(): Array<Role['code']>;
}
