import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Observable, Subscription } from 'rxjs';

export interface PageHeaderAction {
    icon: string;
    position: 'left' | 'right';
    type?: 'default' | 'big' | 'medium';

    isDisabled(): boolean;
    onClick(): void;
}

export interface PageHeaderSearch {
    icon: string;
    placeholder$: Observable<string>;

    onValueChanges(val: string): void;
}

@Component({
    selector: 'epop-page-container-header',
    templateUrl: './page-container-header.component.html',
    styleUrls: ['./page-container-header.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PageContainerHeaderComponent implements OnInit, OnDestroy {
    @Output()
    public readonly searchValue = new EventEmitter<string>();

    @Input()
    public headerActions: Array<PageHeaderAction>;

    @Input()
    public headerSearch: PageHeaderSearch;

    public searchForm = new FormGroup({
        search: new FormControl()
    });

    private searchFormSubscription: Subscription;

    public getActions(position: PageHeaderAction['position']): PageHeaderAction[] {
        return this.headerActions.filter(a => a.position === position);
    }

    public hasSearch(): boolean {
        return !!this.headerSearch;
    }

    public ngOnDestroy(): void {
        this.searchFormSubscription.unsubscribe();
    }

    public ngOnInit(): void {
        this.searchFormSubscription = this.searchForm.valueChanges.subscribe((val: {search: string}) => {
            this.searchValue.emit(val.search);
        });
    }

}
