import { identifier, serializable } from 'serializr';

export const ROLE_ADMIN_ID = 'ADMIN';
export const ROLE_PLANNER_ID = 'PLANNER';
export const ROLE_COLLAB_ID = 'COLLAB';

export class Role {
    @serializable(identifier())
    private id: number;

    @serializable
    private code: string;

    public constructor(id: number, code: string) {
        this.id = id;
        this.code = code;
    }

    public getCode(): string {
        return this.code;
    }

    public getId(): number {
        return this.id;
    }
}
