import { registerLocaleData } from '@angular/common';
import localeEn from '@angular/common/locales/en';
import localeFr from '@angular/common/locales/fr';
import { Injectable } from '@angular/core';
import { DateAdapter } from '@angular/material/core';
import { TranslocoService } from '@ngneat/transloco';
import { enGB, fr } from 'date-fns/esm/locale';

import { LangCode } from './../../models/application-context.model';

interface AppLanguage {
    code: LangCode;
    angularLocaleData: any;
    dateLocale: Locale;
}

export const appLanguages: Record<string, AppLanguage> = {
    french: {
        code: 'fr',
        angularLocaleData: localeFr,
        dateLocale: fr
    },
    english: {
        code: 'en',
        angularLocaleData: localeEn,
        dateLocale: enGB
    }
};


@Injectable({
    providedIn: 'root'
})
export class AppLocalesService {

    public constructor(private translocoService: TranslocoService, private dateAdapter: DateAdapter<any>) {}


    public static getAllAppLangCodes(): Array<string> {
        return Object.values(appLanguages).map(lang => lang.code);
    }

    public initializeAppLocale(): void {
        const currentLang = this.getCurrentAppLanguage();
        registerLocaleData(currentLang.angularLocaleData);
        this.dateAdapter.setLocale(currentLang.dateLocale);
    }

    public getAppLanguageByCode(code: string): AppLanguage {
        return Object.values(appLanguages).find(lang => lang.code === code);
    }

    public getCurrentAppLanguage(): AppLanguage {
        const activeLangCode = this.translocoService.getActiveLang();
        return this.getAppLanguageByCode(activeLangCode);
    }

    /**
     *
     * @returns example : 'fr' or 'en'
     */
    public getCurrentAppLanguageCode(): LangCode {
        return this.getCurrentAppLanguage().code;
    }
}
