import { ChangeDetectionStrategy, Component } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { ICellRendererParams } from 'ag-grid-community';

export type ActionMenuCellParams = Partial<ICellRendererParams> & {
    menuActions?: Array<ActionMenuItem>;
    showEnableToggler?(rowData: unknown): boolean;
    defaultToggleEnable?(rowData: unknown): boolean;
    onEnable?(data: unknown, isEnable: boolean): void;
};


interface ActionMenuItem {
    getLabel(): string;
    onClick?(data: unknown): void;
}

@Component({
    selector: 'epop-action-menu-cell',
    templateUrl: './action-menu-cell.component.html',
    styleUrls: ['./action-menu-cell.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ActionMenuCellComponent implements ICellRendererAngularComp {

    public params: ActionMenuCellParams;

    public hide = true;

    public agInit(params: ICellRendererParams): void {
        this.params = params as ActionMenuCellParams;
        if (this.params.node.data) {
            this.hide = false;
        }
    }

    public refresh(): boolean {
        // By returning false, we let the grid refresh the cell for us.
        return false;
    }

    public onEnableChange(isEnable: boolean): void {
        this.params.onEnable(this.params.node.data, isEnable);
    }

}

