import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { homeRouteResolver } from '../home/home-routing.resolver';
import { errorRouteResolver } from './error-routing.resolver';

@Component({
    selector: 'epop-page-error',
    templateUrl: './error.page.html',
    styleUrls: ['./error.page.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PageErrorComponent implements OnInit {

    public errorCode: string;

    public constructor(private router: Router) {
    }

    ngOnInit(): void {
        this.errorCode = this.router.url.replace(`/${errorRouteResolver.baseRoutePath()}/`, '');
    }

    navigateToDashboard(): void {
        const redirectUrl = homeRouteResolver.baseRoutePath();
        void this.router.navigate([redirectUrl]);
    }
}
