import { ChangeDetectionStrategy, ChangeDetectorRef, Component, HostListener } from '@angular/core';
import { IHeaderParams } from 'ag-grid-community';
import { Observable } from 'rxjs';

// create type with name of column to collapse. Add onclick and collapse function.
export interface ColumnHeaderCollapserParams {
    collapserTitle: string;
    columnToCollapse: Array<string>;
    defaultIsCollapsed: boolean;
    onGridResized$: Observable<void>;
    onCollapse(isCollapsed: boolean): void;
}

@Component({
    selector: 'epop-counter-header-collapser',
    templateUrl: './column-header-collapser.component.html',
    styleUrls: ['./column-header-collapser.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ColumnHeaderCollapserComponent {
    public columnHeaderHeight: number;
    public isCollapsed: boolean;

    private gridWrapper: HTMLElement;
    private params: IHeaderParams & ColumnHeaderCollapserParams;

    public constructor(private cdRef: ChangeDetectorRef) {}

    @HostListener('window:resize', ['$event'])
    public onResize(_event: Event): void {
        this.updateColumnHeight();
    }

    public agInit(params: IHeaderParams & ColumnHeaderCollapserParams): void {
        this.params = params;
        // By default, the panel is open :
        this.isCollapsed = false;
        if (params.defaultIsCollapsed) {
            this.onCollapse();
        }
        if (!this.gridWrapper) {
            this.gridWrapper = document.querySelector('div[ref="rootWrapperBody"]');
            if (this.gridWrapper) {
                this.params.onGridResized$.subscribe(() =>
                    this.updateColumnHeight()
                );
            }
        }
    }

    public getCollapserTitle(): string {
        return this.params.collapserTitle;
    }

    public onCollapse(): void {
        this.isCollapsed = !this.isCollapsed;
        // Update state off all columns :
        this.params.columnToCollapse?.forEach(columnName => {
            const col = this.params.columnApi.getColumn(columnName);
            if (col !== undefined && col.isVisible() === this.isCollapsed) {
                this.params.columnApi.setColumnVisible(columnName, !this.isCollapsed);
            } else if (col === undefined) {
                console.warn(`[ColumnHeaderCollapserComponent] Can't found column with name : ${columnName}`);
            }
        });
        this.params.onCollapse(this.isCollapsed);
    }

    private updateColumnHeight(): void {
        const newHeight = this.gridWrapper.getBoundingClientRect().height;
        if (newHeight !== this.columnHeaderHeight) {
            this.columnHeaderHeight = newHeight;
            this.cdRef.detectChanges();
        }
    }
}
