import { ChangeDetectionStrategy, Component } from '@angular/core';
import { combineLatest, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { Role, ROLE_COLLAB_ID, ROLE_PLANNER_ID } from 'src/app/common/models/role.model';
import { ApplicationContextQuery } from 'src/app/common/store/application-context/application-context.query';
import { ApplicationContextStore } from 'src/app/common/store/application-context/application-context.store';

interface MenuItem {
    icon: string;
    label: string;
    displayed: boolean;
    style?: string;
    action?(): void;
}

@Component({
    selector: 'epop-header-bar-user-dialog',
    templateUrl: './header-bar-user-dialog.component.html',
    styleUrls: ['./header-bar-user-dialog.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class HeaderBarUserDialogComponent extends ApplicationContextQuery {

    public menuItems$: Observable<MenuItem[]> = this.buildMenuItems$();

    public constructor(protected store: ApplicationContextStore) {
        super(store);
    }

    public buildMenuItems$(): Observable<MenuItem[]> {
        return combineLatest([
            this.getCurrentUserRoles$(),
            this.getCurrentUserRole$()
        ]).pipe(
            map(([currentUserRoles, currentUserRole]: [Role[], string]): MenuItem[] => [
                {
                    icon: 'mdi/account-circle-outline',
                    label: 'profil',
                    displayed: true
                },
                {
                    icon: 'mdi/badge-account-outline',
                    label: currentUserRole,
                    displayed: this.canSwitchRole(currentUserRoles, currentUserRole),
                    action: (): void => this.switchRole(currentUserRole)
                },
                {
                    icon: 'mdi/account-tie-outline',
                    label: 'formation',
                    displayed: true
                },
                {
                    icon: 'mdi/logout-variant-1',
                    label: 'logout',
                    displayed: true,
                    style: 'danger'
                }
            ])
        );
    }

    private switchRole(role: string): void {
        this.store.selectRole(this.otherRole(role));
    }

    private otherRole(role: string): string {
        if (!role) {
            return '';
        } else {
            return role === ROLE_COLLAB_ID ?
                ROLE_PLANNER_ID :
                ROLE_COLLAB_ID;
        }
    }

    private canSwitchRole(roles: Role[], currentRole: string): boolean {
        return !!(
            roles?.find(
                role => role.getCode() === this.otherRole(currentRole)
            ));
    }
}
