import { NgModule } from '@angular/core';
import { LoadChildrenCallback, RouterModule, Routes } from '@angular/router';

import { homeRouteResolver } from 'src/app/pages/home/home-routing.resolver';
import { MainLayoutComponent } from 'src/app/pages/main-layout';
import { PlanningRouteResolver } from 'src/app/pages/planning/planning-routing.resolver';
import { PlanningGeneratorRouteResolver } from 'src/app/pages/planning-generator/planning-generator-routing.resolver';
import { UnitsRouteResolver } from 'src/app/pages/units/units-routing.resolver';

import { errorRouteResolver } from './../../../pages/error/error-routing.resolver';
import { PlannerGuard } from './guards/planner.guard';

const routes: Routes = [
    {
        // User context pages :
        path: '',
        component: MainLayoutComponent,
        children: [
            {
                path: homeRouteResolver.configureBasePath(''),
                loadChildren: (): ReturnType<LoadChildrenCallback> =>
                    import('src/app/pages/home/home.module').then(
                        m => m.HomeModule
                    )
            },
            {
                path: PlanningRouteResolver.configureBasePath('planning'),
                canActivate: [PlannerGuard],
                loadChildren: (): ReturnType<LoadChildrenCallback> =>
                    import('src/app/pages/planning/planning.module').then(
                        m => m.PlanningModule
                    )
            },
            {
                path: PlanningGeneratorRouteResolver.configureBasePath('planning-generator'),
                canActivate: [],
                loadChildren: (): ReturnType<LoadChildrenCallback> =>
                    import('src/app/pages/planning-generator/planning-generator.module').then(
                        m => m.PlanningGeneratorModule
                    )
            },
            {
                path: UnitsRouteResolver.configureBasePath('units'),
                canActivate: [PlannerGuard],
                loadChildren: (): ReturnType<LoadChildrenCallback> =>
                    import('src/app/pages/units/units.module').then(
                        m => m.UnitsModule
                    )
            }
        ]
    },
    {
        // Error pages :
        path: errorRouteResolver.configureBasePath('error'),
        loadChildren: (): ReturnType<LoadChildrenCallback> =>
            import('src/app/pages/error/error.module').then(
                m => m.PageErrorModule
            )
    },
    {
        // Fallback when no prior routes is matched :
        path: '**',
        redirectTo: errorRouteResolver.routePath(404)
    }
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }
