import { ChangeDetectionStrategy, Component } from '@angular/core';
import { AgFilterComponent } from 'ag-grid-angular';
import { AgPromise, IDoesFilterPassParams, IFilterParams } from 'ag-grid-community';


export interface ArrayFilterModel<T> {
    values: Array<T>;
}


@Component({
    selector: 'app-array-filter',
    template: '',
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ArrayFilterComponent<T> implements AgFilterComponent {

    public params: IFilterParams;
    public model: ArrayFilterModel<T>;

    isFilterActive(): boolean {
        return this.model?.values && this.model.values.length > 0;
    }

    doesFilterPass(params: IDoesFilterPassParams): boolean {
        const value = this.params.valueGetter(params.node) as Array<T>;
        let pass = true;
        this.model?.values?.forEach(filterVal => {
            if (pass && value.findIndex(v => v === filterVal) < 0) {
                pass = false;
            }
        });
        return pass;
    }

    getModel(): unknown {
        return this.model;
    }

    setModel(model: ArrayFilterModel<T>): void | AgPromise<void> {
        this.model = model;
    }

    agInit(params: IFilterParams): void {
        this.params = params;
    }

}
