import { Injectable } from '@angular/core';

import { RoleSecuredPageGuard } from 'src/app/common/core/routing/guards/secured-page.guard';
import { ROLE_PLANNER_ID } from 'src/app/common/models/role.model';

@Injectable({
    providedIn: 'root'
})
export class PlannerGuard extends RoleSecuredPageGuard {
    getRequiredRole(): string[] {
        return [ROLE_PLANNER_ID];
    }
}
