import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { PageErrorComponent } from './error.page';
import { errorRouteResolver } from './error-routing.resolver';

const routes: Routes = [
    {
        path: errorRouteResolver.route(404).path(),
        component: PageErrorComponent
    },
    {
        path: errorRouteResolver.route(403).path(),
        component: PageErrorComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class ErrorRoutingModule { }
