import { identifier, list, object, serializable } from 'serializr';

import { Role } from './role.model';
export class User {

    @serializable(identifier())
    public id: number = void 0;

    @serializable
    public firstName: string = void 0;

    @serializable
    public lastName: string = void 0;

    @serializable
    public avatarUrl: string = void 0;

    @serializable(list(object(Role)))
    public roles: Array<Role> = undefined;

    @serializable
    public matricule: string = void 0;

    @serializable
    public visa: string = void 0;

    public constructor(init: Partial<User>) {
        Object.assign(this, init);
    }

    public getId(): number {
        return this.id;
    }

    public getFirstname(): string {
        return this.firstName;
    }

    public getLastname(): string {
        return this.lastName;
    }

    public getAvatarUrl(): string {
        return this.avatarUrl;
    }

    public getMatricule(): string {
        return this.matricule;
    }

    public getVisa(): string {
        return this.visa;
    }

    public getRoles(): Array<Role> {
        return this.roles;
    }

    public getUsername(): string {
        return `${this.firstName[0]}.${this.lastName}`;
    }
}
