import { NgModule } from '@angular/core';
import { MatNativeDateModule } from '@angular/material/core';
import { NgxMatNativeDateModule } from '@angular-material-components/datetime-picker';
import {
    TRANSLOCO_CONFIG,
    TRANSLOCO_LOADER,
    TRANSLOCO_MISSING_HANDLER,
    translocoConfig,
    TranslocoModule
} from '@ngneat/transloco';
import { TranslocoMessageFormatModule } from '@ngneat/transloco-messageformat';
import { NgxMatDateFnsDateModule } from 'ngx-mat-datefns-date-adapter';

import { environment } from 'src/environments/environment';

import { appLanguages, AppLocalesService } from './app-locales.service';
import { TranslocoFallbackMissingHandler } from './transloco-fallback-missing-handler';
import { TranslocoHttpLoader } from './transloco-http-loader';

@NgModule({
    imports: [
        TranslocoMessageFormatModule.init(),
        MatNativeDateModule,
        NgxMatDateFnsDateModule,
        NgxMatNativeDateModule
    ],
    exports: [
        TranslocoModule,
        MatNativeDateModule,
        NgxMatNativeDateModule,
        NgxMatDateFnsDateModule
    ],
    providers: [
        {
            provide: TRANSLOCO_CONFIG,
            useValue: translocoConfig({
                availableLangs: Object.values(appLanguages).map(l => l.code),
                defaultLang: appLanguages.french.code,
                prodMode: environment.production,
                reRenderOnLangChange: true
            })
        },
        {
            provide: TRANSLOCO_LOADER,
            useClass: TranslocoHttpLoader
        },
        {
            provide: TRANSLOCO_MISSING_HANDLER,
            useClass: TranslocoFallbackMissingHandler
        }
    ]
})
export class TranslocoRootModule {

    public constructor(appLocalesService: AppLocalesService) {
        appLocalesService.initializeAppLocale();
    }

}
