import { CellRangeParams, Column, GridApi, GridBodyController, GridOptionsWrapper, RowDataTransaction, RowNode } from 'ag-grid-community';


export interface CellNode {
    rowNode: RowNode; column: Column;
}

export class AgGridHelper {

    public static readonly KEY_BACKSPACE = 8;
    public static readonly KEY_DELETE = 46;
    public static readonly DEFAULT_TOOLTIP_DELAY = 800; // in ms

    /**
     * Compute width of the scrollbar of your AGGrid component. If it is not visible, 0 will be return.
     *
     * @param gridApi
     */
    public static getVerticalScrollWitdh(gridApi: GridApi): number {
        // Get grid panel properties. Use of "any" as they didn't provide a api access through it :
        // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access
        const gridPanel = (gridApi as any).gridBodyCon as GridBodyController;
        if (gridPanel.isVerticalScrollShowing()) {
            // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access
            const gridOptionsWrapper = (gridApi as any).gridOptionsWrapper as GridOptionsWrapper;
            return gridOptionsWrapper.getScrollbarWidth();
        }
        return 0;
    }

    public static refreshByTransactions(gridApi: GridApi, currentRowsList: Array<{id: number}>, newRowsList: Array<{id: number}>): void {
        const newRows = [];
        const rowsToUpdate = [];
        const rowsToDelete = [];

        // Row to update or add :
        newRowsList.forEach(row => {
            if (currentRowsList.findIndex(p => p.id === row.id) > -1) {
                rowsToUpdate.push(row);
            } else {
                newRows.push(row);
            }
        });

        // Row to delete :
        currentRowsList.forEach(row => {
            if (newRowsList.findIndex(p => p.id === row.id) === -1) {
                rowsToDelete.push(row);
            }
        });

        // Apply changes to grid : Use of transaction instead of 'setRowData' to allow optimized refresh of cells.
        gridApi?.applyTransaction({
            add: newRows,
            update: rowsToUpdate,
            remove: rowsToDelete
        } as RowDataTransaction);

    }


    /**
     *  Get all selected nodes from selection ranges.
     *
     * @returns
     */
    public static getAllNodeFromRanges(gridApi: GridApi): Array<CellNode> {
        const nodes: Array<CellNode> = [];
        const ranges = gridApi.getCellRanges();
        const rowModel = gridApi.getModel();
        ranges.forEach(range => {
            const startRow = Math.min(range.startRow.rowIndex, range.endRow.rowIndex);
            const endRow = Math.max(range.startRow.rowIndex, range.endRow.rowIndex);
            // eslint-disable-next-line no-loops/no-loops
            for (let rowIndex = startRow; rowIndex <= endRow; rowIndex++) {
                range.columns.forEach(column => {
                    const rowNode = rowModel.getRow(rowIndex);
                    nodes.push({
                        rowNode,
                        column
                    });
                });
            }
        });
        return nodes;
    }

    public static unselectRowFromRanges(gridApi: GridApi, rowToDeselect: number): void {
        const ranges = gridApi.getCellRanges();
        const newRanges: Array<CellRangeParams> = [];
        // Check all ranged and return new ranges without the one specified
        ranges.forEach(range => {
            let startRow = Math.min(range.startRow.rowIndex, range.endRow.rowIndex);
            let endRow = Math.max(range.startRow.rowIndex, range.endRow.rowIndex);
            if (rowToDeselect === startRow) {
                startRow++;
            } else if (rowToDeselect === endRow) {
                endRow--;
            } else if (rowToDeselect > startRow && rowToDeselect < endRow) {
            // Split the range :
                newRanges.push({
                    rowStartIndex: startRow,
                    rowEndIndex: rowToDeselect - 1,
                    columns: range.columns
                });
                // Set startRow for the second splitted part :
                startRow = rowToDeselect + 1;
            }
            if (startRow <= endRow) {
                newRanges.push({
                    rowStartIndex: startRow,
                    rowEndIndex: endRow,
                    columns: range.columns
                });
            }
        });
        // Clean all range selection :
        gridApi.clearRangeSelection();
        // Add the new calculated ones :
        newRanges.forEach(range => gridApi.addCellRange(range));
    }
}
